import { DATATEXT_MUTATION, LOGIN_USERINFO, IS_LOGIN, IS_AUTH,IS_BOL } from './mutation-types';
export default {
    // mutationsstate
    [DATATEXT_MUTATION](state, res) {
        state.datatext = res
    },
    //  
    [LOGIN_USERINFO](state, res) {

        state.userInfo = res
    },
    [IS_LOGIN](state, data) {
        state.isLogin = data;
    },
    [IS_AUTH](state, data) {
        state.is_auth_ok = data;
    },

    [IS_BOL](state, data) {
        state.bol = data;
    }

    
}

